.ui--auth {
  display: grid;
  align-items: center;
}
.ui--auth .container {
  position: relative;
  width: 420px;
  margin: auto;
  padding: 40px;
  border-radius: 20px;
  background-color: var(--base-color);
  box-shadow: 15px 15px 30px #D1D7E0, -15px -15px 30px #F6FCFF;
}
.ui--auth-head {
  margin: 0 0 20px;
  padding: 0 0 10px;
  border-bottom: solid 1px #d8dee7;
}
.ui--auth-head-h2 {
  color: var(--primary-color);
  font-size: 28px;
}
.ui--auth-head-h4 {
  color: var(--gray-color);
  line-height: 2;
  font-size: 14px;
}
.ui--auth-login-input {
  display: grid;
  grid-auto-flow: row;
  grid-row-gap: 20px;
}
.ui--auth-login-or {
  position: relative;
  margin: 30px 0;
}
.ui--auth-login-or .label {
  position: relative;
  z-index: 1;
  display: block;
  margin: 0 auto;
  width: 60px;
  height: 60px;
  line-height: 60px;
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  color: var(--primary-color);
  border-radius: 100%;
  background-color: var(--base-color);
  box-shadow: 6px 6px 20px #D1D7E0, -6px -6px 20px #FFF;
}
.ui--auth-login-or .line {
  position: absolute;
  top: 55%;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #d3dae4;
}
.ui--auth-login-other {
  display: grid;
  grid-auto-flow: row;
  grid-row-gap: 15px;
}
.ui--auth-login-main {
  transition: all 0.4s ease;
}
.ui--auth-login-main.hidden {
  opacity: 0 !important;
  pointer-events: none !important;
}
.ui--auth-login-sso {
  position: absolute;
  z-index: 10;
  top: 136px;
  left: 40px;
  right: 40px;
  bottom: 76px;
  display: grid;
  grid-template-rows: auto 90px 144px 40px;
  align-content: center;
  background-color: var(--base-color);
  /*
      * Enter and leave animations can use different
      * durations and timing functions.
      **/
}
.ui--auth-login-sso.fade-enter-active {
  transition: all 0.6s ease;
}
.ui--auth-login-sso.fade-enter {
  opacity: 0;
  transform: scale(1.05);
}
.ui--auth-login-sso .type {
  text-align: center;
  padding: 0 0 25px;
  font-size: 12px;
  color: var(--gray-color);
}
.ui--auth-login-sso .avatar {
  display: block;
  height: 90px;
  padding: 15px;
  margin: 0 auto;
  border-radius: 100%;
  color: var(--primary-color);
  background-color: var(--base-color);
  box-shadow: 6px 6px 20px #D1D7E0, -6px -6px 20px #FFF;
}
.ui--auth-login-sso .info {
  text-align: center;
  display: grid;
  grid-row-gap: 8px;
  padding: 20px 0 40px;
}
.ui--auth-login-sso .info:not(.has-data) > * {
  margin: 0 auto;
  border-radius: 2px;
  color: #D4D9E2 !important;
  background-color: #D4D9E2 !important;
  animation: text-loader 1s infinite linear;
}
.ui--auth-login-sso .info:not(.has-data) .code {
  width: 90px;
  animation-delay: 0s;
}
.ui--auth-login-sso .info:not(.has-data) .name {
  width: 250px;
  animation-delay: 0.2s;
}
.ui--auth-login-sso .info:not(.has-data) .title {
  width: 180px;
  animation-delay: 0.4s;
}
.ui--auth-login-sso .info .code {
  text-transform: uppercase;
}
.ui--auth-login-sso .info .name {
  font-weight: 600;
  text-transform: capitalize;
}
.ui--auth-login-sso .info .title {
  font-size: 15px;
  color: var(--gray-color);
}
@keyframes text-loader {
  0%,
  100% {
    transform: translateY(0);
  }
  60% {
    transform: translateY(-2px);
  }
}
.ui--auth-login-sso .footer {
  display: grid;
  grid-column-gap: 10px;
  grid-template-columns: 50px 1fr;
}
.ui--auth-login-sso .footer .btn-default {
  padding: 0 15px;
}
.ui--auth-login-sso .footer .btn-default .icon {
  width: 25px;
  height: 25px;
}
.ui--auth-login-sso iframe {
  position: absolute;
  z-index: -1;
  width: 0;
  height: 0;
  opacity: 0;
  pointer-events: none;
}
.ui--auth-pwd-main {
  display: grid;
  grid-auto-flow: row;
  grid-row-gap: 20px;
}
.ui--auth-pwd-button {
  display: grid;
  grid-column-gap: 10px;
  grid-template-columns: 50px 1fr;
}
.ui--auth-pwd-button .btn-default {
  padding: 0 15px;
}
.ui--auth-pwd-button .btn-default .icon {
  width: 25px;
  height: 25px;
}
.ui--auth-copyright {
  margin: 20px 0 0;
  text-align: center;
  color: var(--gray-color);
  font-size: var(--font-size-small);
}
